.countdown {
    z-index: 3;
    position: relative;
    overflow: hidden;

    padding: 175px 0 182px;

    background: #0a41d1 url('../images/countdown/countdown-bg.jpg') 0 center / cover no-repeat;

    @media (max-width:$md2+px){
        padding: 100px 0;
    }

    &::before {
        z-index: -1;
        content: "";
        position: absolute;
        opacity: .9 ;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgb(7,29,104);
        background: linear-gradient(345deg, rgba(7,29,104,1) 0%, rgba(11,75,236,1) 100%);
    }

    &__inner {
    }

    &__items {
        display: flex;
        justify-content: space-between;

        @media (max-width:$md2+px){
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr 1fr;
            grid-gap: 100px 30px;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        color: #fff;

        max-width: 200px;

        @media (max-width:$md2+px){
            margin: 0 auto;
            text-align: center;
        }
    }

    &__icon {
        margin-bottom: 60px;
        font-size: 56px;
    }

    &__count {
        margin: 0px 0px 20px 0px;
        @include font-size(52px);
        font-weight: 600;
    }

    &__text {
        @include font-size(24px);
        font-weight: 300;
    }
}
