.service {

    padding: 158px 0;

    @media (max-width:$md2+px){
        padding: 70px 0 100px;
    }

    &__inner {
        position: relative;
    }

    &__suptitle {
    }

    &__title {
    }

    &__content {
        margin: 70px 0px 0px 0px;
    }

    &__slider {
    }

    &__slider-item {
        display: flex;
        flex-direction: column;
        text-align: center;

        border: 1px solid #b3b3b3;
        min-height: 0;
    }

    &__slider-img {
        width: 100%;
    }

    &__slider-text {
        padding: 32px 15px;
        flex: 1;
        @include font-size(20px);
        color: #333333;
    }

    &__slider-button {
        display: none;

        @media (min-width:$md2+px){
            display: block;
            position: absolute;
            bottom: -60px;
    
            cursor: pointer;
            
        }
    }

    &__slider-button--prev {
        left: 50px;
    }
    &__slider-button--next {
        right: 50px;
    }
}

.service__slider-item.swiper-slide {
    height: unset;
}

.service__slider.swiper-wrapper {
    position: unset;
}

.service__content.swiper-container {
    position: unset;
}

//Swiper Navigation

.service__slider-button.swiper-button-disabled {
    opacity: .2;
    pointer-events: none;
}

//Swiper Pagination

.service__slider-pagination {
    display: none;
    @media (max-width:$md2+px){
        display: block;
    }
}

.swiper-container-horizontal>.service__slider-pagination.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: -56px;
}