//Primary style=================================
body {
    min-height: 100%;
    
    color: #000;
    font-family: $fontFamily;
    font-weight: 400;
    font-size: 14px;
}

footer {
    flex-shrink: 0;
}
//Primary style=================================

//Modules=======================================
.btn {
    font-size: 14px;
    font-weight: 600;
    color: #0b4bec;
    background-color: #fff;
    border: 1px solid transparent;

    &:hover {
        background-color: #0a42d2;
        color: #fff;
        border: 1px solid #fff
    }
}

.suptitle {
    @include font-size(24px);
    font-weight: 300;
    line-height: 1.8;
    color: #808080;
    text-transform: uppercase;
}

.title {
    @include font-size(30px);
    font-weight: 600;
    text-transform: uppercase;
}



//Modules=======================================

//Wrapper=======================================
.wrapper {
    width: 100%;
    min-height: 100%;
    // overflow-x: hidden;
    display: flex;
    flex-direction: column;

    &::after {
        display: block;
        content: '';
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,.5);
        opacity: 0;
        pointer-events: none;
        will-change: opacity;
        transition: opacity 0.3s cubic-bezier(0,0,0.3,1);
        overflow: hidden;

        position: fixed;
        top: 0;
        left: 0;
    }
    &.active::after {
        opacity: 1;
        pointer-events: auto;
    }
}
//Wrapper=======================================

//Main Block===================================
.page {
    flex: 1 0 auto;
}
//Main Block===================================

//Main Grid==================================
.container {
    max-width: $maxWidthContainer + px;
    margin: 0 auto;
    padding: 0 15px;
