.footer {
    z-index: 3;
    position: relative;
    padding: 41px 0 73px;
    background: #0a41d1 url('../images/footer/footer-bg.jpg') 0 center / cover no-repeat;

    &::before {
        z-index: -1;
        content: "";
        position: absolute;
        opacity: .9 ;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgb(7,29,104);
        background: linear-gradient(345deg, rgba(7,29,104,1) 0%, rgba(11,75,236,1) 100%);
    }
    &__content {
        text-align: center;
        color: #fff;
        line-height: 1.5;
    }

    &__copyright {
    }
}