.process {

    background-color: #f3f2f2;
    padding: 160px 0 201px;

    @media (max-width:$md2+px){
        padding: 70px 0 80px;
    }

    &__inner {
        text-align: center;
    }

    &__title {
        margin: 0px 0px 100px 0px;

        @media (max-width:$md2+px){
            margin: 0px 0px 50px 0px;
        }
    }

    &__items {
        display: flex;
        justify-content: center;

        @media (max-width:$md2+px){
            flex-direction: column;
            align-items: center;
        }
    }

    &__item {
        margin: 0px 50px 0px 0px;
        padding: 49px 39px;
        max-width: 370px;

        background-color: #fff;

        transition: all .2s ease-in-out;

        @media (max-width:$md2+px){
            margin: 0px 0px 30px 0px;
        }

        &:last-child {
            margin: 0;
        }

        @media (min-width:$md2+px){
            &:hover {
                background-color: rgb(7,29,104);
                background: linear-gradient(345deg, rgba(7,29,104,1) 0%, rgba(11,75,236,1) 100%);
                
                transform: scale(1.2);
    
                .process__item-title,
                .process__item-text {
                    color: #fff;
                }
            }
        }

    }

    &__item-img-wrap {
        margin: 0px auto 26px auto;
        max-width: 110px;
    }

    &__item-img {
    }

    &__item-title {
        margin: 0px 0px 35px 0px;
        @include font-size(20px);
        color: #000;
        text-transform: uppercase;
    }

    &__item-text {
        font-family: "Raleway";
        font-size: 14px;
        line-height: 28px;
        color: #808080;
    }
}