.header {

    position: fixed;
    width: 100%;

    z-index: 100;

    background: linear-gradient(to top, rgba(7,29,104,.2) 0%, rgba(11,75,236,1) 100%);
    background-color: #0a41d1;
    box-shadow: 0px 8px 8px 0px rgba(34, 60, 80, 0.2);
    
    &__content {
        display: flex;
        align-items: center;
        min-height: 120px;

        @media (max-width:$md2+px){
            justify-content: space-between;
            min-height: 75px;
        }
    }

    &__logo {
        display: flex;
        color: #fff;
        text-align: left;
    }

    &__logo-icon {
        margin: 0px 15px 0px 0px;
        width: 40px;
        height: 43px;
        object-fit: cover;
    }

    &__logo-text {
        display: flex;
        flex-direction: column;
        line-height: 0.7;
        justify-content: flex-end;
    }

    &__logo-title {
        margin: 0px 0px 10px 0px;
        text-transform: uppercase;
        font-size: 18px;
        text-shadow: 0px 3px 3px rgba(30, 28, 28, 0.75);

    }

    &__logo-subtitle {
        font-size: 12px;
        font-weight: 300;
    }

    &__menu {
        display: flex;
        align-items: center;
        margin-left: auto;

        @media (max-width:$md2+px){
            z-index: 5;
            display: block;
            position: fixed;
            overflow: auto;
            top: 75px;
            right: 0;
            bottom: 0;
            padding: 60px 30px 30px;
            width: 100%;
            height: 100%;
            max-width: 330px;

            background: linear-gradient(to top, rgba(7,29,104,.5) 0%, rgba(11,75,236,1) 100%);
            background-color: #15338f;

            transform-origin: 0% 0%;
            transform: translate3d(100%,0,0);
            visibility: hidden;
            pointer-events: none;
        
            transition: all .45s cubic-bezier(.77,.2,.05,1);

            &.active {
                visibility: visible;
                transform: translate3d(0,0,0);
                pointer-events: auto;
            }
        }
    }

    &__btn {
        padding: 5px 13px;
        border-radius: 12px;
    }
}

.menu {
    margin: 0px 37px 0px 0px;
    display: flex;

    @media (max-width:$md2+px){
        display: block;
        margin: 0px 0px 30px 0px;
    }

    &__item {
        margin: 0px 42px 0px 0px;
        &:last-child {
            margin: 0;
        }

        @media (max-width:$md2+px){
            margin: 0px 0px 20px 0px;
        }
    }

    &__link {
        text-transform: uppercase;
        color: #fff;
        position: relative;

        &:hover {
            &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 2px;
                left: 0;
                right: 0;
                bottom: -2px;
                background-color: #fff;
            }
        }


        @media (max-width:$md2+px){
            font-size: 18px;
        }
    }
}

.nav-toggle {
    @media (max-width:$md2+px){
        z-index: 10;
        position: relative;
        width: 40px;
        height: 40px;
        background-color: #fff;
        border-radius: 4px;

        cursor: pointer;

    &__icon {
        display: inline-block;
        position: absolute;
        left: 50%;
        top: 50%;
        bottom: auto;
        right: auto;
        transform: translateX(-50%) translateY(-50%);
        width: 18px;
        height: 2px;
        background-color: $accent-color;

        border-radius: 4px;

        &::before, &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;

            border-radius: 4px;
            background-color: inherit;
        }

        &::before {
            bottom: 5px;
        }

        &::after {
            top: 5px;
        }
    }

    &.active {
        .nav-toggle__icon {
            background-color: rgba($accent-color, 0);

            &:before, &:after {
                background-color: rgba($accent-color, 1);
            }
    
            &:before {
                bottom: 0;
                transform: rotate(45deg);
            }
    
            &:after {
                top: 0;
                transform: rotate(-45deg);
            }
        }
    }
    }
}