.message {

    padding: 137px 0 148px;

    @media (max-width:$md4+px){
        padding: 100px 0 110px;
    }

    &__form {
        display: flex;
        justify-content: space-between;

        @media (max-width:$md4+px){
            flex-direction: column;
        }
    }

    &__form-title {
        margin: 0px 0px 50px 0px;
        font-size: 30px;
        font-weight: 600;

        @media (max-width:$md4+px){
            text-align: center;
        }
    }

    &__form-col-1,
    &__form-col-2 {
        display: flex;
        flex-direction: column;
        width: 47%;

        @media (max-width:$md4+px){
            width: 100%;
            align-items: center;
        }
    }

    &__form-input,
    &__form-textarea {
        border: 1px solid #cccccc;
        padding: 12px 20px 10px;
        width: 100%;

        &:focus {
            box-shadow: 0 0 5px #0b4bec;
        }

        &::placeholder {
            font-size: 14px;
            font-family: "Raleway";
            color: #808080;
        }

        @media (max-width:$md4+px){
            width: 80%;
        }

        @media (max-width: 425px){
            width: 100%;
        }
    }

    &__form-col-1 {
        @media (max-width:$md4+px){
            margin: 0px 0px 50px 0px;
        }
    }

    &__form-input {
        margin: 0px 0px 30px 0px;
        &:last-child {
            margin: 0;
        }
    }

    &__form-col-2 {
    }

    &__form-textarea {
        margin: 0px 0px 30px 0px;
        resize: none;
        height: 100%;
        max-height: 130px;

        @media (max-width:$md4+px){
            min-height: 130px;
        }
    }

    &__form-btn {
        padding: 12px 20px 10px;
        background-color: #0b4bec;
        color: #fff;

        &:hover {
            background-color: #163177;
        }

        @media (max-width:$md4+px){
            width: 80%;
        }

        @media (max-width: 425px){
            width: 100%;
        }
    }
}